body {
  background-color: lightgray;
}
#container {
  display: grid;
  height: 100vh;
}
#drum-machine {
  width: 340px;
  height: 220px;
  display: grid;
  grid-template-columns: 55px 55px 55px;
  grid-row-gap: 7px;
  grid-column-gap: 7px;
  justify-self: center;
  align-self: center;
  background-color: #0c2340;
  border-radius: 10px;
  border: 2px solid #e31937;
  padding: 12px;
}
.drum-pad {
  background-color: #e31937;
  border-color: #e31937;
  color: white;
}

#display {
  color: white;
  justify-self: end;
  align-self: end;
  border: 1px solid white;
}
